"use client";

import React from "react";

import { clients } from "@/data";
import { InfiniteMovingCards } from "./ui/InfiniteCards";

const Clients = () => {
  return (
    <section id="clients" className=" mt-[180px]">
      <h1 className="heading">
        <span className="text-purple"> Clients</span>
      </h1>

      <div className="flex flex-col items-center max-lg:mt-6">
        <div
          // remove bg-white dark:bg-black dark:bg-grid-white/[0.05], h-[40rem] to 30rem , md:h-[30rem] are for the responsive design
          className="h-[50vh] md:h-[30rem] rounded-md flex flex-col antialiased  items-center justify-center relative overflow-hidden"
        >
          <div className="flex flex-wrap items-center justify-center gap-4 md:gap-16 max-lg:mt-4">
            {clients.map((client) => (
              <React.Fragment key={client.id}>
                <div className="flex md:max-w-60 max-w-32 gap-2">
                  <img
                    src={client.img}
                    alt={client.name}
                    className="w-[55px] md:w-[110px] rounded-full"

                  />
                </div>
              </React.Fragment>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Clients;
